.Log {
  background-color: rgb(219, 144, 31);
  width: 100%;
  height: 40px;
  position: -webkit-sticky;
  position: fixed!important;
}

button {
  float: right;
  margin-right: 20px;
}
table td{
  cursor: pointer;
}

.pro-sidebar {
   /* height: 100vh!important; */
   color: #adadad;
   height: 100%;
   width: 230px;
   min-width: 230px;
   text-align: left;
   transition: width, left, right, 0.3s;
   position: relative;
   z-index: 1009;
   margin-top: 140px;
   margin-bottom: 140px!important;
}
.pro-sidebar-inner{
    position: fixed!important;
    width: 230px;
    min-width: 230px;
}
.pro-sidebar .pro-menu {
    padding-top: 10px;
    padding-bottom: 10px;
    margin-bottom: 140px;
}
